footer{
    position: relative;
    width: 100%;
    height: 80% !important;
    padding: 50px 100px;
    background: black;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.fas {
    color:goldenrod!important;
}

footer .container
{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-direction: row;
}

footer .container .sec{
    margin-right: 30px;
    margin-top: -55px;
}

footer .container .sec.aboutus{
    width: 40%;
}

footer .container h4{
    position: relative;
    color: white;
    font-weight: 500;
    margin-bottom: 15px;
}

footer .container h4::before{
    content: '';
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 50px;
    height: 2px;
    background: goldenrod
}

footer p{
    color: rgb(172, 166, 166);
}

.sci{
    margin-top: 20px;
    display: flex;
    margin-left: -36px;
}
.sci li{
    list-style: none;
}

.sci li a{
    display: inline-block;
    width: 40px;
    height: 40px;
    background: #222;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
    text-decoration:none;
    border-radius: 50px;
}

.sci li a:hover{
    background: #FFD700;
}

.sci li a .fab{
    color: #fff;
    font-size: 17px;
}

.quickLinks{
    position: relative;
    width: 25%;
}

.quickLinks ul li{
    list-style: none;
}
.quickLinks ul li a{
    color: #999;
    text-decoration: none;
    margin-bottom: 10px;
    display: inline-block;
}

.info li i{
    color: gold;
}

.quickLinks ul li a:hover{
    color: #fff;
}

.contact{
    width: calc(35% - 60px);
    margin-right: 0 !important;
}

.contact .info{
    position: relative;
}

.contact .info li{
    display: flex;
    margin-bottom: 16px;
}
.contact .info li span:nth-child(1){
    color: #fff;
    font-size: 17px;
    margin-right: 10px;
}

.contact .info li span{
    color: #999;
}

.contact .info li a{
    color: #999;
    text-decoration: none;
}

.contact .info li a:hover{
    color: #fff;
}

@media (max-width: 991px)
{
    footer{
        padding: 40px;
    }
    footer .container{
        flex-direction: column;
    }
    footer .container .sec{
        margin-right: 0;
        margin-bottom: 40px;
    }
    footer .container .sec.aboutus,.quickLinks,.contact{
        width: 100%;
    }
}
